import { useSvgDrawing } from 'react-hooks-svgdrawing';
import { Classable } from '../../types';
import { cn } from '../../utils';
import './Whiteboard.css';

interface WhiteboardProps extends Classable {}

export default function Whiteboard({ className }: WhiteboardProps) {
  const [renderRef, draw] = useSvgDrawing({
    penWidth: 10,
    penColor: '#ff0000',
    delay: 60,
  });

  return (
    <div>
      <div ref={renderRef} className={cn('whiteboard', className)} />
      <button onClick={draw.clear}>clear</button>
      <button onClick={draw.undo}>undo</button>
    </div>
  );
}
